import { GooglePlacesInput } from "components/global-components/Components/Form";
import { MapDisplay } from "components/global-components/Components/SingleMap";
import StreetViewMap from "components/global-components/Components/StreetView/StreetViewMap";
import { Field, useFormikContext } from "formik";
import { fetchAddress } from "lib/helpers/reverse-geocode";
import { isEmpty } from "lodash";
import React,{ useState } from "react";
import { Col, Row } from "reactstrap";
import Stats from "./Stats";

export const ValidateAddress = (props) => {
  const [addressLoc, setAddressLoc] = useState();
  const [isValidAddress, setIsValidAddress] = useState(false);
  const { setFieldValue } = useFormikContext();

  const handleValidateAddress = (nextStep) => {
    if (isValidAddress) {
      props.onChangeAddress(true);
    }
  };

  const handleChange = (address) => {
    setFieldValue("property_address", address);
    setAddressLoc(null);
    setIsValidAddress(false);
  };

  const handleSelect = (address, location) => {
    
    setAddressLoc(location);
    setIsValidAddress(true);
  };

  const handleMarkerDragEnd = async (newPosition) => {
    const newAddress = await fetchAddress(newPosition);
    
    setAddressLoc(newPosition);
    setFieldValue("property_address", newAddress);
    // You can perform additional actions with the updated position if needed
    setIsValidAddress(true);
  };

  return (
    <>
      <div
        style={{
          position: "relative",
          minHeight: "250px",
        }}
      >
        <Col xs="12" sm="12" md="12">
          <Field
            name="property_address"
            required
            label="Address"
            searchType={["address"]}
            placeholder="Enter Street, City, State, Zip"
            childFieldName="map_marker"
            onSelect={handleSelect}
            onChange={handleChange}
            isEdit={true}
            component={GooglePlacesInput}
          />
          <p>
            This step is to validate your address to further proceed with adding
            your property details.
          </p>
        </Col>

        {!isEmpty(addressLoc) && (
          <Row className=" mb-5  ">
            <Col md={6}>
              <MapDisplay
                location={addressLoc}
                onMarkerDragEnd={handleMarkerDragEnd}
              />
            </Col>

            <Col md={6}>
              <StreetViewMap center={addressLoc} />
            </Col>
          </Row>
        )}
        {isValidAddress && (
          <Stats {...props} validateStep={true} onValidateAddress={handleValidateAddress} />
        )}
      </div>
    </>
  );
};
