import { FormikHOC } from "components/global-components/Components/ReactStrapFormik/";
import { useFormikContext } from "formik";
import { fetchAddress } from "lib/helpers/reverse-geocode";
import React, { useState } from "react";
import { connect } from "react-redux";
import StepWizard from "react-step-wizard";
import { ValidationSchema } from "../Form/validationSchema";
import AddressDetailsForm from "../Tabs/AddressDetailsForm";
import HouseDetailsForm from "../Tabs/HouseDetailsForm";
import LeaseDetailsForm from "../Tabs/LeaseDetailsForm";
import Nav from "./Nav";
import { ValidateAddress } from "./validateAddress";

const CreateList = ({
  toggleModal,
  toggleCongratulation,
  isEditMode = false,
  ...props
}) => {
  const [isValidAddress, setIsValidAddress] = useState(isEditMode);

  return (
    <StepWizard
      isHashEnabled
      initialStep={1}
      nav={
        <Nav
          newItem={
            !isEditMode && !isValidAddress
              ? [{ text: "Add Property Details", icon: "fas fa-home" }]
              : []
          }
          onStepClick={(index) => index === 4 && toggleModal()}
          {...props}
        />
      }
    >
      {!isEditMode && !isValidAddress && (
        <ValidateAddress
          isValidAddress={isValidAddress}
          hashKey={"FirstStep"}
          {...props}
          onChangeAddress={(isValid) => setIsValidAddress(isValid)}
        />
      )}
      {isValidAddress && <AddressDetailsForm {...props} />}
      {isEditMode && <HouseDetailsForm {...props} />}
      {isEditMode && (
        <LeaseDetailsForm
          hashKey={"TheEnd!"}
          {...props}
          toggleModal={toggleModal}
          onPackageSelect={(i) => toggleCongratulation(true)}
        />
      )}
    </StepWizard>
  );
};

const mapStateToProps = (state, props) => ({
  validationSchema: ValidationSchema,
  submitButtonLabel: "Save ",
  isFormikSubmit: false,
  validateOnChange: true,
});
const mapDispatchToProps = (dispatch) => ({});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FormikHOC(CreateList));
