import React from "react";
import LoadingButton from "components/global-components/Components/Buttons/LoadingButton";

export default function Stats({
  currentStep,
  firstStep,
  goToStep,
  lastStep,
  nextStep,
  previousStep,
  totalSteps = 4,
  step,
  handleSubmit,
  isSubmitting,
  update,
  handleResponse,
  item = { is_published: false },
  toggleNavigation,
  setFieldValue,
  setFormEmpty,
  onValidateAddress,
  validateStep = false,
  ...props
}) {
  console.log("State isSubmitting", isSubmitting);
  const finishStep = () => {
    props.history.push("/listing");
    // setFormEmpty();
    window.location = window.location.origin;
  };
  return (
    <div>
      <hr />

      {currentStep > 1 && (
        <button
          className="border btn btn-default btn-block mb-2"
          onClick={previousStep}
        >
          Go Back
        </button>
      )}
      {currentStep === 1 && (
        <LoadingButton
          label="Continue"
          btnclass="btn-block"
          isLoading={isSubmitting}
          onClick={() => {
            if (validateStep) {
              onValidateAddress(nextStep);
            } else {
              handleSubmit();
            }
          }}
        />
      )}
      {/* {currentStep === 1 && (
        <LoadingButton
          label="Continue"
          btnclass="btn-block"
          isLoading={isSubmitting}
          onClick={() => {
          }}
        />
      )} */}
      {currentStep === 2 && (
        <LoadingButton
          label={`${item.is_published ? "Un Publish" : "Publish"}`}
          btnclass="btn-block w-100"
          // isLoading={isSubmitting}
          onClick={() => {
            setFieldValue("is_published", item.is_published ? false : true);
            setFieldValue("is_publish_changed", true);
            handleSubmit();
          }}
        />
      )}
      {currentStep === 3 && (
        <button
          className="btn btn-success btn-block"
          onClick={
            () => finishStep()

            // setFormEmpty(() => (window.location.href = window.location.origin));
            // setFormEmpty();
          }
        >
          Finish
        </button>
      )}

      <hr />
    </div>
  );
}
