import { isNull } from 'lodash';
import isEmpty from "lodash/isEmpty";
import React, { useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Row } from "reactstrap";
import ListingTabs from "./Components/ListingTabs";
import { initialValues } from "./Form/InitialValues.json";

const AddListing = ({
  getIdMode,
  item,
  listingImages,
  onEditItem,
  isImageUploading,
  location,
  ...props
}) => {

  let isEditMode = !isNull(getIdMode({...location}));

  const setInitialValues = () => {
    if (isEditMode && !isEmpty(item)) {
      const { display_id, ...rest } = item;
      return { id: display_id, display_id, ...rest, ...listingImages };
    } else {
      return initialValues;
    }
  };

  useEffect(() => {
    ;
    if (isEditMode && isEmpty(item)) {
      console.log("props.location.pathname", location)
      const id = location.pathname.split("/")[2];
     
       onEditItem({ _id: id });
    }
  }, []);

  return isImageUploading ? (
    <Row className="flex-column text-center">
      <img
        style={{ height: 250 }}
        className="w-25 align-self-center"
        src={require("../../../../assets/img/setting_listings.svg")}
        alt=""
      />
      <h5>Please wait we are setting up your listing....</h5>
    </Row>
  ) : props.isLoading ? (
    <Row className="flex-column text-center">
      <img
        style={{ height: 250 }}
        className="w-25 align-self-center"
        src={require("../../../../assets/img/loading_listing.svg")}
        alt=""
      />
      <h5>Loading....</h5>
    </Row>
  ) : (
    <ListingTabs
      isEditMode={isEditMode}
      initialValues={setInitialValues()}
      item={item}
      {...props}
    />
  );
};
export default withRouter(AddListing);
