import React, { useState, useEffect } from "react";

const calculateDestinationCoordinates = (lat, lng, distance) => {
  const earthRadius = 6371000; // Earth's radius in meters

  // Convert distance from meters to radians
  const angularDistance = distance / earthRadius;

  // Convert latitude and longitude to radians
  const latRad = (lat * Math.PI) / 180;
  const lngRad = (lng * Math.PI) / 180;

  // Calculate new latitude
  const newLatRad = Math.asin(
    Math.sin(latRad) * Math.cos(angularDistance) +
      Math.cos(latRad) * Math.sin(angularDistance) * Math.cos(0)
  );

  // Calculate new longitude
  const newLngRad =
    lngRad +
    Math.atan2(
      Math.sin(0) * Math.sin(angularDistance) * Math.cos(latRad),
      Math.cos(angularDistance) - Math.sin(latRad) * Math.sin(newLatRad)
    );

  // Convert new latitude and longitude to degrees
  const newLat = (newLatRad * 180) / Math.PI;
  const newLng = (newLngRad * 180) / Math.PI;

  return { lat: newLat, lng: newLng };
};

export const useReverseGeocoding = (loc) => {
  const [address, setAddress] = useState("");
  const [error, setError] = useState(null);
  const [location, setLocation] = useState(loc);
  const [mapCenter, setMapCenter] = useState();

  //   useEffect(() => {
  //     setLocation(loc);
  //   },[loc])

  const getAddress = (locat) => {
    return setLocation(locat);
  };

  const getBearingDistance = () => {
    return location !== null
      ? calculateDestinationCoordinates(location.lat, location.lng, -0.5)
      : null;
  };

  useEffect(() => {
    const fetchAddress = async () => {
      try {
        const response = await fetch(
          `https://maps.googleapis.com/maps/api/geocode/json?latlng=${location.lat},${location.lng}&key=${process.env.REACT_APP_GOOGLE_MAP_KEY}`
        );
        if (response.ok) {
          const data = await response.json();
          if (data.results.length > 0) {
            setAddress(data.results[0].formatted_address);
          } else {
            setAddress("Address not found");
          }
        } else {
          setError("Error fetching address");
        }
      } catch (error) {
        console.error("Error fetching address:", error);
        setError("Error fetching address");
      }
    };

    if (location != null) {
      fetchAddress();
    }
  }, [location]);

  return { address, error, getAddress, getBearingDistance };
};

export const fetchAddress = async (location) => {
  try {
    const response = await fetch(
      `https://maps.googleapis.com/maps/api/geocode/json?latlng=${location.lat},${location.lng}&key=${process.env.REACT_APP_GOOGLE_MAP_KEY}`
    );
    if (response.ok) {
      const data = await response.json();
      if (data.results.length > 0) {
        return data.results[0].formatted_address;
      }
    }
  } catch (error) {
    console.error("Error fetching address:", error);
  }
};
